.ui {
    margin-top: 3rem;
    display: flex;
    column-gap: 2rem;
}



.filter {
    border-radius: 10px;
    background-color: $card-color;
    padding: 1rem;
    max-height: 450px;

    p {
        font-size: 1.2rem;
        font-family: $font-heading;
        font-weight: 500;
        margin-bottom: 2rem;
    }

    form {
        min-width: 220px;
        display: flex;
        flex-direction: column;
        font-family: $font-body;

        label {
            display: block;
            margin-bottom: 10px;
        }

        input {
            min-width: 100%;
            background-color: $field-color;
            border: none;
            outline: none;
            color: $text-color;
            padding: 0.5rem;
        }

        .toprow {
            margin-bottom: 2rem;
        }

        .lowrow {
            display: flex;
            flex-direction: column;

            div {
                margin-bottom: 2rem;
            }

            .dropdown {
                display: flex;
                padding: 0;

                select {
                    width: 100%;
                    background-color: $field-color;
                    border: none;
                    outline: none;
                    color: $text-color;
                    padding: 0.5rem;
                }

                svg {
                    padding: 0.5rem 0.7rem;
                    background: $button-color;
                    color: $light-color;
                }
            }

            button {
                padding: 0.3rem 1rem;
                align-self: flex-end;
                border: none;
                background: $button-color;
                color: $light-color;
                font-weight: 500;
                transition: all 0.4s ease;
                cursor: pointer;

                &:hover {
                    background: $field-color;
                }
            }
        }


    }
}


.list {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    flex-wrap: wrap;
}

.item {
    border-radius: 10px;
    background: $card-color;
    display: flex;
    box-shadow: inset 0px 1px 5px rgba(45, 78, 225, 0.813);

    .img {
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
        min-width: 200px;
        min-height: 200px;
        background: #000;
    }

    .info {
        padding: 1rem;

        p:nth-child(2) {
            margin-top: 5px;
        }

        p:nth-child(3) {
            margin-top: 1rem;
        }
    }

    .number {
        margin-right: 2rem;
        max-width: 70px;
        display: flex;
        justify-content: center;
        align-items: center;

        p {
            background-color: $button-color;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            display: grid;
            place-content: center;
            font-size: 1.2rem;
        }
    }


}

@media screen and (max-width:768px) {
    .filter {
        width: 100%;

        form .lowrow {
            flex-direction: row;
            gap: 1rem;

            div {
                margin: 0;
            }

            .orderby {
                flex: 1;
            }

            button {
                align-self: flex-end;
                padding: 0.5rem 1.5rem;

            }
        }

    }

    .ui {
        flex-wrap: wrap;
    }

    .list {
        margin-top: 4rem;
    }
}

@media screen and (max-width:480px) {
    .filter {
        height: 400px;

        form .lowrow {
            flex-direction: column;
            gap: 1rem;
            height: 100px;

            button {
                width: 100%;
                padding: 0.5rem 1rem;
            }

            div {
                margin-bottom: 5px;

            }

        }


    }

    .ui {
        border: 1px solid red;
        flex-wrap: wrap;
    }

    .list {
        margin-top: 4rem;
    }

    .item {
        flex-direction: column;
        position: relative;

        .info {
            h3 {
                font-size: 2rem;
            }
        }

        .number {
            position: absolute;
            right: -20px;
            top: 20px;
        }

    }


}